section {
  max-width: 450px;
  max-height: 58vh;
  min-height: 58vh; /*to stay always full width*/
  margin: 20px auto;
  margin-bottom: 0px !important;
  overflow: auto;
  overflow-x: hidden;
}

section::-webkit-scrollbar {
  background-color: #f5f5f5;
  width: 10px;
}

section::-webkit-scrollbar-thumb {
  border-radius: 50px;
  background: linear-gradient(to left, #fff, #e4e4e4);
  border: 1px solid #ccc;
}

section div {
  max-width: 200px;
  word-wrap: break-word;
  margin-bottom: 0px;
  line-height: 20px;
}

section div:not(:last-child):after {
  content: "";
  display: table;
  clear: both;
}

.from-me {
  position: relative;
  /*  padding: 10px 20px;*/
  padding: 10px 20px 5px 20px;
  color: #323838;
  background: #bfecc1;
  border-radius: 10px;
  float: left;
  font-size: 14px;
  margin-bottom: 1.5em;
}

.bottom {
  border-color: #c8c8c8 transparent transparent transparent;
  border-width: 8px 8px 0px 8px;
  bottom: -8px;
}

.from-me:before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 20px;
  width: 0;
  height: 0;
  border: 20px solid transparent;
  border-top-color: #bfecc1;
  border-bottom: 0;
  border-left: 0;
  margin-left: -10px;
  margin-bottom: -12px;
}

.from-them {
  position: relative;
  padding: 10px 20px 5px 20px;
  background: #c3dbe7;
  border-radius: 10px;
  color: #323838;
  float: right;
  font-size: 14px;
  margin-bottom: 1.5em;
}

.from-them:before {
  content: "";
  position: absolute;
  bottom: 0;
  right: 10px;
  width: 0;
  height: 0;
  border: 20px solid transparent;
  border-top-color: #c3dbe7;
  border-bottom: 0;
  border-right: 0;
  margin-left: -10px;
  margin-bottom: -12px;
}

.metadata {
  display: inline-block;
  float: right;
  padding: 0 0 0 7px;
  position: relative;
  bottom: 0px;
}
.metadata .time {
  color: #777;
  font-size: 13px;
  display: inline-block;
}

.input-padding {
  padding-right: 4em;
}

.input-group-addon {
  display: flex;
  align-items: center;
  padding-left: 4px;
}

input:focus {
  box-shadow: none !important;
}

.from-me p,
.from-them p {
  text-align: left !important;
}

.input-button {
  bottom: 5px;
  z-index: 2;
  margin-left: -1em;
  margin: 5px auto;
  background-color: #f3f3f3;
}

.none {
  display: none;
}

.input-group {
  margin-bottom: 0.5em;
  margin-top: 1.5em;
}

.input-group-addon {
  color: #fff;
  background: #56b36c;
  padding-right: 1em;
}

.form-control:focus {
  border-color: #fff;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(255, 0, 0, 0.6);
}

